.article__info-product {
    position: relative;
}

.article__info-product .section__location-services ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.article__info-product .section__location-services ul li {
    display: flex;
    align-items: center;
    width: calc(50% - .5em);
    margin-bottom: 1.5em;
}

.article__info-product .section__location-services ul li p {
    width: calc(100% - 32px);
    font-size: 14px;
}

.article__info-product .section__location-services ul li::before {
    content: "";
    display: block;
    width: 22px;
    height: 22px;
    margin-right: .5em;
}

.article__info-product .section__location-services ul li.li__cocina::before {
    background: url(../../assets/icons/kitchen-orange.svg) no-repeat center center;
    background-size: contain;
}

.article__info-product .section__location-services ul li.li__estacionamiento-gratuito::before {
    background: url(../../assets/icons/parking-orange.svg) no-repeat center center;
    background-size: contain;
}

.article__info-product .section__location-services ul li.li__televisor::before {
    background: url(../../assets/icons/tv-orange.svg) no-repeat center center;
    background-size: contain;
}

.article__info-product .section__location-services ul li.li__pileta::before {
    background: url(../../assets/icons/pool-orange.svg) no-repeat center center;
    background-size: contain;
}

.article__info-product .section__location-services ul li.li__aire-acondicionado::before {
    background: url(../../assets/icons/air-conditioning-orange.svg) no-repeat center center;
    background-size: contain;
}

.article__info-product .section__location-services ul li.li__wifi::before {
    background: url(../../assets/icons/wifi-orange.svg) no-repeat center center;
    background-size: contain;
}

.article__info-product .section__location-services ul li.li__apto-mascotas::before {
    background: url(../../assets/icons/pets-orange.svg) no-repeat center center;
    background-size: contain;
}

@media (min-width: 1024px) {
    .article__info-product .section__location-services > h2 {
        margin-bottom: 0;
    }

    .article__info-product .section__location-services ul {
        justify-content: flex-start;
    }

    .article__info-product .section__location-services ul li {
        width: calc(25% - 1.5em);
        margin: 3em 1.5em 3em 0;
    }

    .article__info-product .section__location-services ul li:nth-child(4n+4) {
        margin-right: 0;
    }
}